<template>
  <div>
    <header>
      <div class = 'content'>
        <span>种类：</span>
        <dropDown class = 'typeDrop' :dropData = doorType @dropChange = 'typeChange'>
        </dropDown>

        <span>宽度：</span>
        <el-input class = 'input' v-model = curWidth type = 'number' placeholder = '请输入宽度'></el-input>

        <span>高度：</span>
        <el-input class = 'input' v-model = curHeight type = 'number' placeholder = '请输入高度'></el-input>

        <span>有无下坎：</span>
        <dropDown class = 'typeDrop' :dropData = frameData @dropChange = 'frameChange'>
        </dropDown>

        <span>数量：</span>
        <el-input class = 'input' v-model = curNumber type = 'number' placeholder = '请输入数量'></el-input>

        <el-button class = 'confirm' type = 'primary' @click = 'confirm'>确定</el-button>
        <el-button class = 'confirm2' type = 'primary' @click = 'deleteOne'>删除上一个</el-button>
      </div>
    </header>

    <main>
      <el-button class = 'confirm' type = 'primary' @click = 'createPdf'>
        下载 PDF 文件
      </el-button>
      <br>

      <div class = 'imgDiv' id = 'imgDivId' ref = 'imgDom' v-if = 'planData.length > 0'>
        <template v-for = '(data, index) in planData'>
          <div class = 'plan' :key = index>
            <div style="display: flex; justify-content: space-around">
              <span class="fontBold">{{data.openType}}</span>
              <span class="fontBold">{{data.underFrame}}</span>
              <span>尺寸：{{data.size}} * {{data.number}}</span>
            </div>

            <div style="width: 100%; height: 50px; display: flex; flex-wrap: wrap">
              <template v-for = '(dat, ind) in data.data'>
                <div class = 'planDetail'>
                  <span>{{dat.title}}：{{dat.content}}</span>
                </div>
              </template>
            </div>
          </div>

          <div style = 'width: 800px; height:65px; background-color: #ffffff'
               v-if = '(index + 1) % 22 == 0'>
          </div>
        </template>
      </div>

    </main>
  </div>
</template>

<script>
  import dropDown from '@/components/dropDown'
  import { getData } from '../../js/main2'
  import html2canvas from 'html2canvas'
  import JsPDF from 'jspdf'

  const doorType = [
    { type: 0, title: '双开门'},
    { type: 1, title: '单开门'}
  ]

  const frameData = [
    { type: 0, title: '有下坎'},
    { type: 1, title: '无下坎'}
  ]

  export default {
    name: 'index',

    components: { dropDown },

    data () {
      return {
        doorType: doorType,
        curType: doorType[0].type,
        curWidth: '',
        curHeight: '',
        curNumber: '',
        frameData: frameData,
        underFrame: frameData[0].type,

        planData: [],
      }
    },

    methods: {
      typeChange (value) {
        this.curType = value.type
      },

      frameChange (value) {
        this.underFrame = value.type;
      },

      confirm () {
        if(this.curWidth != '' && this.curHeight != '' && this.curNumber != '') {
          let plan =
            getData(this.curType,this.curWidth,this.curHeight,this.underFrame,this.curNumber)
          this.planData.push(plan)
        }
      },

      deleteOne () {
        this.planData.pop()
      },

      createPdf () {
        window.pageYOffset = 0;
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;

        html2canvas(this.$refs.imgDom, {
          allowTaint: true,
          scale: 2,
          dpi: 182,//导出图片清晰度
        }).then(function (canvas) {
          let contentWidth = canvas.width
          let contentHeight = canvas.height
          //一页pdf显示html页面生成的canvas高度;
          let pageHeight = contentWidth / 592.28 * 841.89
          //未生成pdf的html页面高度
          let leftHeight = contentHeight
          //页面偏移
          let position = 0
          let imgWidth = 595.28
          let imgHeight = 592.28 / contentWidth * contentHeight
          //返回图片dataURL，参数：图片格式和清晰度(0-1)
          let pageData = canvas.toDataURL('image/jpeg', 1.0)
          //方向默认竖直，尺寸ponits，格式 a4纸 [595.28,841.89]
          let PDF = new JsPDF('', 'pt', 'a4')
          //有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
          //当内容未超过pdf一页显示的范围，无需分页
          if (leftHeight < pageHeight) {
            //addImage将图片添加到pdf中
            //addImage中间两个参数控制x、y边距，
            //后两个参数控制添加图片的尺寸，此处将页面高度按照a4纸宽高比列进行压缩
            PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
          } else {
            while (leftHeight > 0) {
              PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
              leftHeight -= pageHeight
              position -= 841.89
              //避免添加空白页
              if (leftHeight > 0) {
                //addPage()添加pdf页数
                PDF.addPage()
              }
            }
          }
          //保存名称
          PDF.save('计算结果.pdf')
        })
      }
    }
  }
</script>

<style scoped>
  header {
    width: 100%;
    /*min-width: 1920px;*/
    height: 80px;
    color: #ffffff;
    border-bottom: 1px solid #dcdfe6;
    position: fixed;
    top: 0;
  }
  header .content {
    /*width: 1920px;*/
    width: 100%;
    height: 100%;
    /*margin: 0 auto;*/
    /*padding-top: 20px;*/
    /*padding-bottom: 20px;*/
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    align-items: center;
    background-color: #fff;
  }
  header .content span {
    color: #000000;
    margin-left: 30px;
  }
  header .content .input {
    width: 120px;
    height: 40px;
    font-size: 17px;
  }
  header .content .confirm {
    width: 80px;
    height: 40px;
    margin-left: 40px;
  }
  header .content .confirm2 {
    width: 100px;
    height: 40px;
    margin-left: 40px;
    padding: 0;
  }
  main {
    width: 100%;
    height: auto;
    padding-top: 100px;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    -webkit-flex-direction: column;
    flex-direction: column;
  }
  .imgDiv {
    width: 800px;
    height: auto;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 20px 20px 20px 20px;
  }
  main .plan {
    width: 400px;
    height: auto;
    box-sizing: border-box;
    margin-top: 10px;
    border: 1px dashed #000000;
    padding: 10px;
  }
  main .plan .planTitle {
    width: 100%;
    text-align: center;
    float: left;
    margin-bottom: 10px;
  }
  main .plan .planDetail {
    width: 50%;
    padding-top: 5px;
  }
  .fontBold{
    font-weight: bold;
    font-size: 15px;
  }
</style>



