
function getData(openType, width, height, underFrame, number) {
  if(openType === 0) {      //双开
    let aWidth = (width - 90) / 2 + 88;
    let bWidth = (width - 90) / 2 + 63;
    let cWidth = (width - 90) / 2 + 108;
    let dWidth = (width - 90) / 2 + 41;

    let acHeight, bdHeight;

    if(underFrame === 0) {          //有下坎
      acHeight = height - 65;
      bdHeight = height - 62;
    }else {                         //无下坎
      acHeight = height - 35;
      bdHeight = height - 32;
    }
    return {
      openType: openType === 0? '双开门' : '单开门',
      size: width + ' * ' + height,
      underFrame: underFrame === 0? '有下坎' : '无下坎',
      number: number,
      data: [
        {title: 'A', content: aWidth + ' * ' + acHeight + ' * ' + number},
        {title: 'B', content: bWidth + ' * ' + bdHeight + ' * ' + number},
        {title: 'C', content: cWidth + ' * ' + acHeight + ' * ' + number},
        {title: 'D', content: dWidth + ' * ' + bdHeight + ' * ' + number},
      ]
    }
  }else {                   //单开
    let aWidth = width - 90 + 88;
    let bWidth = width - 63;
    let aHeight = height - 65;
    let bHeight = height - 62;

    return {
      openType: openType === 0? '双开门' : '单开门',
      size: width + ' * ' + height,
      underFrame: underFrame === 0? '有下坎' : '无下坎',
      number: number,
      data: [
        {title: 'A', content: aWidth + ' * ' + aHeight + ' * ' + number},
        {title: 'B', content: bWidth + ' * ' + bHeight + ' * ' + number}
      ]

    }
  }
}

export { getData }



